"use strict";

import preload from "./modules/preload.js";
import observer from "./services/observer.js";
import showStyles from "./modules/styles.js";
import showProducts from "./modules/products.js";
import showSmallMenu from "./modules/smallMenu.js";
import showTopMenuAndArrows from "./modules/showTopMenu.js";
import hamburger from "./modules/hamburger.js";

import { stylesImg, stylesLink, modal } from "./services/variables.js";

window.addEventListener("DOMContentLoaded", () => {
	preload(".overlay", "body", "style");
	observer("Delay", ".animate");

	if (window.innerWidth >= 992) {
		listener("click");
	} else {
		listener("touchend");
	}

	window.addEventListener("scroll", showTopMenuAndArrows);

	function listener(action) {
		window.addEventListener(action, (e) => {
			if (
				e.target.classList.contains("style__show") ||
				e.target.closest(".btn__styles")
			) {
				showStyles(
					e.target,
					".btn__styles",
					"styles__link",
					"styles__title",
					"style__show"
				);
			} else if (e.target.closest(".products")) {
				showProducts(
					"menu__full_white",
					"menu__forbigproduct",
					"show",
					"overflow_hidden"
				);
			} else if (e.target.closest(".menu__subtitle")) {
				showSmallMenu(e.target, "show", "height", "overflow_scroll");
			} else if (
				e.target.closest(".hamburger") ||
				e.target.classList.contains(".hamburger")
			) {
				hamburger(
					"menu__full_white",
					"menu__forbigproduct",
					"menu__big_show",
					"menu__list_hide",
					"menu__display_none",
					"overflow_scroll",
					"overflow_hidden",
					"show",
					"height"
				);
			} else if (e.target.closest(".modal__close")) {
				modal.classList.remove("show__modal");
			}
		});
	}

	stylesLink.forEach((item, i) => {
		item.addEventListener("mouseover", () => {
			stylesImg[i].classList.add("show");
		});
		item.addEventListener("mouseout", () => {
			stylesImg[i].classList.remove("show");
		});
	});

	const form = document.querySelector(".footer__form");

	form.addEventListener("submit", function (e) {
		e.preventDefault();

		fetch("mailer/smart.php", {
			method: "POST",
			body: new FormData(this),
		})
			.then(function (response) {
				if (response.ok) {
					form.querySelectorAll("input").forEach((input) => {
						console.log(1);
						input.value = "";
					});
					form.reset();
				}
			})
			.catch((error) => {
				console.log("Error:", error);
			});

		return false;
	});
});
